@import url(https://fonts.googleapis.com/css2?family=Arvo&family=Chela+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.CategoryButton {
    border-radius: 20px;
    margin-left: 30px;
    margin-right: 30px;
    width: 100px;
    border: none;
    background-color: #e8e8e8;
    cursor: pointer;
}

.IconContainer {
    text-align: center;
    padding: 20px;
    border-radius: 50%;
    transition-duration: 0.2s;
    color: #411557;
    font-size: 2.5em;
}

.CategoryLabel {
    font-family: 'Arvo', serif;
    font-weight: bold;
    font-size: 14px;
    text-align: center;
    padding: 5px;
    color: white;
    transition-duration: 0.2s;
}

.CategoryButton:hover .IconContainer{
    background-color: #411557;
    color: #00beb7;
}
.CategoryButton:hover .CategoryLabel{
    color: #411557;
}

.CategoryButton:focus .IconContainer{
    background-color: #411557;
    color: #00beb7;
}
.CategoryButton:focus .CategoryLabel{
    color: #411557;
}

.card-container {
    position: relative;
    width: 40%;
    margin-top: 20px;
    margin-left: 15px;
    margin-right: 15px;
    padding: 10px;
    height: auto;
    font-family: 'Arvo', serif;
    line-height: 1.8;
}

h1 {
    color: #411557;
    margin-right: 100px;
}

.card-footer {
    height: 70px;
}

.btn-wrapper {
    position: absolute;
    bottom: 15px;
    right: 15px;
}

.price-wrapper {
    position: absolute;
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
    right:15px;
    top:35px;
}

.quantity-btn-wrapper {
    position: absolute;
    bottom: 20px;
    left: 15px;
}

.inc-btn {
    border:none;
    background-color: white;
    font-size: 25px;
    cursor: pointer;
}

.dec-btn {
    border:none;
    background-color: white;
    font-size: 30px;
    cursor: pointer;
}

.inc-btn:active {
    font-weight: bold;
}

.dec-btn:active {
    font-weight: bold;
}

.quantity-input {
    width: 30px;
    font-size: 20px;
    text-align: center;
}

.add-btn {
    background-color: #411557;
    color: white;
    font-size: 15px;
    font-weight: bold;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
    border-radius: 16px;
    border: none;
    cursor: pointer;
}

.add-btn:active {
    background-color: #00beb7;
    color: #411557;
}
.item-container {
    position: relative;
    font-family: 'Arvo', serif;
    padding: 10px;
    color: #411557;
}

.inner-text-container {
    margin: 10px;
    padding-left: 20px;
}

.item-name {
    font-size: 18px;
}

.desc {
    color: darkgray;
    font-size: 12px;
}

.qty {
    position: absolute;
    top: 20px;
}

.price {
    position: absolute;
    top: 20px;
    right: 10px;
}

.remove-btn {
    color: red;
    font-size: 12px;
    font-weight: bold;
    border: none;
    background: transparent;
    cursor: pointer;
}
.icon-container {
    position: fixed;
    z-index: 1;
    font-size: 1.9em;
    background-color: #3c3c3b;
    border: none;
    border-radius: 50%;
    padding: 18px;
    bottom: 25px;
    right: 25px;
    cursor: pointer;
}

.counter {
    position: fixed;
    font-size: 12px;
    background-color: #00beb7;
    border-radius: 30px;
    padding: 5px;
    bottom: 25px;
    right: 25px;
}

.expand-cart {
    position: fixed;
    z-index: 2;
    background-color: white;
    width: 600px;
    right: -600px;
    top: 170px;
    bottom: 0px;
    overflow: scroll;
    transition: 0.4s;
}

.outer-cart-wrapper {
    padding: 30px;
}

.cart-header-wrapper {
    position: fixed;
    z-index: 3;
    width: 600px;
    height: 100px;
    right: 0;
    top: 70px;
    background-color: #f5f5f5;
    line-height: 100px;
}

.cart-header {
    font-family: 'Arvo', serif;
    font-size: 26px;
    color: black;
    padding-left: 30px;
}

.close-cart {
    position: absolute;
    background-color: transparent;
    right: 30px;
    top: 38px;
    border: none;
    font-size: 20px;
    cursor: pointer;
}

.back-btn {
    position: absolute;
    background-color: transparent;
    right: 90px;
    top: 35px;
    border: none;
    font-size: 26px;
    cursor: pointer;
}

.cart-footer {
    line-height: 2.2;
}

.checkout-btn {
    background-color: #411557;
    color: white;
    font-size: 15px;
    font-weight: bold;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
    border-radius: 16px;
    margin-top: 20px;
    border: none;
    cursor: pointer;
}

.cart-total {
    color: black;
}

.cart-counter {
    color: black;
}

.right-side {
    position: absolute;
    right: 30px;
}

.form {
    padding: 30px;
    line-height: 2.2;
}

.text-input {
    font-family: 'Arvo', serif;
    font-size: 16px;
    width: 100%;
    border: none;
}

.error {
    position: absolute;
    color: red;
    font-size: 12px;
    right: 30px;
    padding: 5px;
}

.submit-btn {
    background-color: #411557;
    color: white;
    font-size: 15px;
    font-weight: bold;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 15px;
    padding-left: 15px;
    border-radius: 16px;
    margin-top: 20px;
    border: none;
    cursor: pointer;
}

@-webkit-keyframes pulse {
    0% {
        box-shadow: 0 0 0 0 rgba(0,0,0,1);
    }

    100% {
        box-shadow: 0 0 0 25px rgba(0,0,0,0);
    }
}

@keyframes pulse {
    0% {
        box-shadow: 0 0 0 0 rgba(0,0,0,1);
    }

    100% {
        box-shadow: 0 0 0 25px rgba(0,0,0,0);
    }
}
.menu-wrapper {
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    top: 230px;
    position: relative;
}

.header-wrapper {
    width: 80%;
    margin: auto;
    padding: 20px;
}

.category-header {
    height: 100px;
    background-color: #00beb7;
    color: white;
    font-family: 'Arvo', serif;
    font-size: 30px;
    line-height: 100px;
    padding-left: 20px;
}

.menu-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    max-width: 1200px;
    margin: 0 auto;
}

.MenuHeader {
    position: fixed;
    z-index: 1;
    display: -webkit-flex;
    display: flex;
    top: 70px;
    transition: top 0.5s;
    background-color: #e8e8e8;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    height: 120px;
    -webkit-justify-content: center;
            justify-content: center;
    overflow-y: hidden;
    overflow-x: scroll;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* FireFox */
}

.logo {
    display: block;
    width: 50%;
    padding-top: 10%;
    margin-left: auto;
    margin-right: auto;
}

.header {
    position: fixed;
    top: 0px;
    width: 100%;
    height: 70px;
    z-index: 2;
    background-color: white;
}

.title-link {
    position: absolute;
    color: #411557;
    font-family: 'Chela One', cursive;
    text-align: center;
    text-decoration: none;
    font-size: 40px;
    top: 12px;
    left: 100px;
}

.header-link {
    position: absolute;
    top: 25px;
    right: 20px;
    float: right;
    font-size: 22px;
    font-family: 'Arvo', serif;
    font-weight: bolder;
    text-decoration: none;
}

.header-link::after {    
    position: absolute;
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 3px;
    left: 50%;
    background: #411557;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
  }
.header-link:hover::after { 
    width: 100%; 
    left: 0; 
}
